.swiper-container {
	margin-left: auto;
	margin-right: auto;
	position: relative;
	overflow: hidden;
	list-style: none;
	padding: 0;
	z-index: 1;

	.swiper-wrapper {
		position: relative;
		width: 100%;
		height: 100%;
		z-index: 1;
		display: flex;
		transition-property: transform;
		box-sizing: content-box;

		.swiper-slide {
			flex-shrink: 0;
			width: 100%;
			height: 100%;
			position: relative;
			transition-property: transform;
		}
	}

	&.swiper-container-multirow > .swiper-wrapper {
		flex-wrap: wrap;
	}

	&.swiper-container-multirow-column > .swiper-wrapper {
		flex-wrap: wrap;
		flex-direction: column;
	}
}


@mixin goods-inform-medium {
	.feature-list {
		margin-top: 0.5rem;

		.feature-item {
			margin-right: 0.5rem;
			@include font-body(xx-small);
		}
	}

	.goods-evaluate {
		display: flex;
		margin-top: 0.25rem;

		.goods-rating {
			display: flex;
			flex-wrap: wrap;
			margin-right: 0.5rem;
			@include font-body(x-small);
			color: $color-base-060;
			font-family: 'Noto Sans KR, sans-serif';

			.head {
				@include hidden;
			}
			.body {
				display: flex;
				&::before {
					content: '';
					display: block;
					width: 1.5em;
					height: 1.5em;
					background-repeat: no-repeat;
					background-position: center center;
					background-size: 0.75rem auto;
					background-image: svgIcon(star, $color-base-060);
				}
			}
		}

		.cumulative-sales {
			display: flex;
			flex-wrap: wrap;
			margin-right: 0.5rem;
			@include font-body(x-small);
			color: $color-base-060;
			font-family: 'Noto Sans KR, sans-serif';
			.head {
				margin-right: 0.25rem;
			}
			.body {
				display: flex;
			}
		}
	}

	.goods-head {
		order: 2;
		margin-top: 0.25rem;

		.goods-title {
			order: 3;
			@include font-body(small);

			.goods-name {
				min-height: calc(1.25rem * 2);
				@include ellipsis(2);
			}
		}
		.goods-noti {
			order: 1;
			@include font-body(x-small);
		}

		.goods-promotional {
			order: 2;
			@include font-body(x-small);
		}
	}

	.goods-body {
		order: 3;
	}

	.goods-foot {	
		.goods-price {
			align-items: center;

			.price-sale {
				.value {
					@include font-body(medium);
				}
				.unit {
					margin-left: 0.125rem;
					@include font-body(small);
				}
			}

			.price-discount {
				margin-left: 0.25rem;
				@include font-body(small);
			}

			.price-calc {
				@include font-body(x-small);
				color: $color-base-060;
				margin-left: 0.25rem;
			}
		}
	}
}

@mixin goods-inform-small {
	.goods-head {
		order: 2;
		margin-top: 0.25rem;

		.goods-title {
			@include font-body(x-small);

			.goods-name {
				@include ellipsis(2);
			}
		}
	}
	.goods-foot {
		.goods-price {
			.price-sale {
				.value {
					@include font-body(small);
				}
				.unit {
					@include font-body(x-small);
				}
			}
			.price-discount {
				margin-left: 0.25rem;
				@include font-body(x-small);
			}
		}
