}

@mixin goods-inform {
	display: flex;
	flex-direction: column;

	.goods-head {
		display: flex;
		flex-direction: column;

		.goods-title {
			.goods-name {
				color: $color-base-070;
				word-break: break-all;
			}

			a.goods-name {
				&:not(:hover) {
					text-decoration: none;
				}
			}
		}

		.goods-noti {
			color: $color-accent-070;
		}

		.goods-promotional {
			color: $color-primary-080;
		}
	}

	.goods-body {
		.feature-list {
			display: flex;
			flex-wrap: wrap;
	
			.feature-item {
				font-weight: 700;
				color: $color-base-070;
	
				&.lowest-price,
				&.coupon,
				&.free-shipping {
					color: $color-accent-070;
				}
	
				&.cumulative-sales {
					font-weight: 400;
					color: $color-base-060;
					justify-self: end;
				}
			}
		}
	}

	.goods-foot {
		order: 1;
		display: flex;
		align-items: center;

		.goods-price {
			display: flex;
			flex-wrap: wrap;

			.price-list {
				display: flex;
				align-items: center;
				width: 100%;
			}

			.price-sale {
				display: flex;
				align-items: center;
				color: $color-base-090;
				
				.value {
					font-family: 'Lato', sans-serif;
					font-weight: 700;
					text-decoration: none;
				}
				.unit {
					font-weight: 400;
				}
			}

			.price-discount {
				display: flex;
				align-items: center;
				text-decoration: none;
				color: $color-primary-080;
				font-family: 'Lato', sans-serif;
				font-weight: 700;
			}
		}
	}
}

.goods-figure {
	.goods-label {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: absolute;


		&.recommend {
			top: 0;
			left: 0;
			width: 2.25rem;
			height: 2.25rem;
			font-weight: 700;
			@include font-body(xx-small);
			color: #fff;
			border-top-left-radius: 0.25rem;
			border-bottom-right-radius: 0.25rem;
			background-color: rgba(0,0,0,0.8);
		}
		
		&.time-deal {
			top: 0;
			left: 0;
			width: 2.25rem;
			height: 2.25rem;
			font-weight: 700;
			@include font-body(xx-small);
			color: $color-base-090;
			border-bottom-right-radius: 0.25rem;
			background-color: $color-secondary-070;

			&::before {
				content: '';
				display: block;
				margin-bottom: -0.125rem;
				width: 1rem;
				height: 1rem;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 100% auto;
				background-image: svgIcon(timedeal, $color-base-090);
			}
		}

		&.soldout {
			right: 0;
			bottom: 0;
			left: 0;
			height: 1.25rem;
			@include font-body(xx-small);
			color: #fff;
			font-weight: 700;
			text-align: center;
			// border-bottom-left-radius: 0.25rem;
			// border-bottom-right-radius: 0.25rem;
			background-color: rgba(0,0,0,0.4);
		}

		&.similar {
			top: 0;
			left: 0;
			height: 1.25rem;
			padding: 0 0.25rem;
			font-weight: 700;
			@include font-body(xx-small);
			color: #fff;
			border-top-left-radius: 0.25rem;
			border-bottom-right-radius: 0.25rem;
			background-color: rgba(0,0,0,0.4);
		}

		&.new {
			top: 0;
			left: 0;
			width: 2.25rem;
			height: 2.25rem;
			font-weight: 700;
			line-height: 0.75rem;
			font-size: 0.625rem;
			color: #fff;
			border-bottom-right-radius: 0.25rem;
			background-color: $color-primary-070;

			&::before {
				content: '';
				display: block;
				width: 1rem;
				height: 1rem;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 100% auto;
				background-image: svgIcon(new, #fff);
			}
		}

		&.schedule {
			top: 0;
			left: 0;
			width: 2.25rem;
			height: 2.25rem;
			font-weight: 700;
			line-height: 0.75rem;
			font-size: 0.625rem;
			color: #fff;
			border-top-left-radius: 0.25rem;
			border-bottom-right-radius: 0.25rem;
			background-color: rgba(0,0,0,0.6);
		}
	}
}

.goods-display {
	//goods-filter
	.info-board.no-data {
		margin: 4rem 0;
	}
	.goods-filter {
		position: relative;

		&::after {
			content: '';
			display: block;
			position: absolute;
			bottom: 0;
			left: calc(50% - 50vw);
			width: 100vw;
			height: 0.0625rem;
			background-color: $color-base-020;
		}

		.filter-head {
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 3rem;

			.filter-title {
				.filter-name {
					@include hidden;
				}
				.filter-data {
					display: block;
					@include font-body(small);
					font-weight: 400;
					color: $color-base-090;
				}
			}

			.filter-intro {
				position: relative;
			}
		}

		.filter-body {
			padding-bottom: 0.75rem;
			
			.filter-list {
				position: relative;
				left: calc(50% - 50vw);
				padding: 0 0.5rem;
				
				@include respond-above(x-small) {
					padding: 0 1rem;
				}

				@include respond-above(small) {
					padding: 0 1.25rem;
				}

				box-sizing: border-box;
				width: 100vw;
				white-space: nowrap;
				overflow-x: auto;
				-webkit-overflow-scrolling: touch;
				-ms-overflow-style: none;
				scrollbar-width: none;
				&::-webkit-scrollbar {
					display: none;
				}
			}
		
			.filter-item {
				display: inline-flex;
				align-items: center;
				padding: 0 0.25rem 0 0.5rem;
				height: 1.75rem;
				@include font-body(x-small);
				color: #fff;
				white-space: nowrap;
				font-family: 'Noto Sans KR', sans-serif;
				border-radius: 0.125rem;
				background-color: $color-primary-070;
		
				// +.filter-item {
				// 	margin-left: 0.25rem;
				// }
		
				.btn.delete {
					margin-left: 0.25rem;
					width: 1rem;
					height: 1rem;
					fill: #fff;
		
					svg {
						width: 100%;
						height: 100%;
					}
				}
			}
		}
	}

	.goods-sort {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0.5rem 0;
	}

	>.button-area.style-a {
		margin: 2rem 0;
	}

	// goods-list
	.goods-list {
		margin: 0;
		padding: 0;

		.goods-item {
			list-style: none;

			.goods-area {
				.goods-figure {
					position: relative;
					background-color: $color-base-010;
	
					.goods-thumbnail {
						.image {
							display: block;
							width: 100%;
							
							&.noimage {
								opacity: 0.25;
							}
						}
					}

					.goods-scrap {
						position: absolute;
						display: block;
						right: 0;
						bottom: 0;
						padding: 0.5rem;
	
						svg {
							fill: #fff;
							filter: drop-shadow(0 0.125rem 0.1875rem rgba(0,0,0,0.25));
						}
	
						&.checked {
							svg {
								fill: $color-primary-070;
							}
						}
					}
				}
			}
		}
	}
	
	&.style-a {
		.goods-list {
			.goods-item {
				.goods-rank {
					@include font-body(medium);
					font-weight: 700;
					margin-bottom: 0.25rem;
					color: $color-base-090;
				}
				.goods-area {
					position: relative;

					.goods-figure {
						overflow: hidden;
						border-radius: 0.25rem;
					}
					.goods-inform {
						@include goods-inform;
					}
				}
			}
		}

		&.type-a {
			&.attr-scroll {
				display: flex;
				position: relative;
				left: calc(50% - 50vw);
				width: 100vw;
				box-sizing: border-box;
				overflow-x: auto;
				-webkit-overflow-scrolling: touch;
				-ms-overflow-style: none;
				scrollbar-width: none;
				&::-webkit-scrollbar {display: none;}
	
				.goods-list {
					flex-grow: 1;
					display: flex;
					padding: 0 0.5rem;
					@include respond-above(x-small) {padding: 0 1rem;}
					@include respond-above(small) {padding: 0 1.25rem;}
	
					.goods-item {
						+.goods-item {
							margin-left: 0.5rem;
						}
					}
				}
			}

			&.x-small {
				.goods-list {
					.goods-item {
						width: 6rem;
						min-width: 6rem;

						.goods-area {
							.goods-figure {
								+.goods-inform {
