	&.style-b {
		&.type-a {
			display: flex;
			position: relative;
			left: calc(50% - 50vw);
			margin: -1rem 0;
			padding: 1rem 0;
			width: 100vw;
			box-sizing: border-box;
			overflow-x: auto;
			-webkit-overflow-scrolling: touch;
			-ms-overflow-style: none;
			scrollbar-width: none;
			&::-webkit-scrollbar {display: none;}
	
			.goods-list {
				flex-grow: 1;
				display: flex;
				padding: 0 0.5rem;
				@include respond-above(x-small) {padding: 0 1rem;}
				@include respond-above(small) {padding: 0 1.25rem;}
				
				.goods-item {
					box-shadow: 0 2px 4px 0 rgba(0,0,0,0.08);
	
					+.goods-item {
						margin-top: 0;
						margin-left: 0.5rem;
					}
	
					.goods-area {
						.goods-figure {
							overflow: hidden;
							border-top-right-radius: 0.25rem;
							border-top-left-radius: 0.25rem;
						}
	
						.goods-inform {
							padding: 1rem;
							@include goods-inform;
						}
					}
				}
			}

			&.medium {
				.goods-list {
					.goods-item {
						width: 14rem;
						flex-basis: 14rem;

						.goods-area {
							.goods-inform {
								@include goods-inform-medium;
							}
						}
					}
				}
			}

			&.small {
				.goods-list {
					.goods-item {
						width: 8.75rem;
						flex-basis: 8.75rem;

						.goods-area {
							.goods-inform {
								.goods-head {
									.goods-title {
										@include font-body(small);
							
										.goods-name {
											@include ellipsis(2);
										}
									}
								}
								.goods-foot {
									margin-top: 0.75rem;
									order: 2;

									.goods-price {
										.price-sale {
											.value {
												@include font-body(small);
												color: $color-base-090;
											}
											.unit {
												@include font-body(x-small);
											}
										}
										.price-discount {
											margin-left: 0.25rem;
											@include font-body(x-small);
											font-weight: 400;
										}
									}
								}
							}
						}
					}
				}
			}

			&.attr-scroll {
				margin: 0 0 -1rem;
				padding: 0 0 1rem;
			}
		}
	}

	// style-c
	&.style-c {
		.goods-list {
			.goods-item {
				+.goods-item {
					margin-top: 1rem;
				}
				.goods-area {
					box-shadow: 0 0.125rem 0.25rem 0 rgba(0,0,0,0.08);
	
					.goods-figure {
						position: relative;
	
						.goods-thumbnail {
							display: block;
							height: 8rem;
							border-top-right-radius: 0.25rem;
							border-top-left-radius: 0.25rem;
							background-position: left center;
							background-size: 100% auto;
						}
	
						.goods-thumbnail-magnify {
							display: block;
							overflow: hidden;
							position: absolute;
							bottom: -1.5rem;
							right: 1.25rem;
							width: 4.25rem;
							height: 4.25rem;
							border: 0.125rem solid #fff;
							border-radius: 50%;
							box-sizing: border-box;
	
							img {
								display: block;
								width: 100%;
								height: 100%;
							}
						}
					}
	
					.goods-inform {
						padding: 1rem 1rem 0.25rem 1rem;
						border-bottom-right-radius: 0.25rem;
						border-bottom-left-radius: 0.25rem;
						background-color: #fff;
	
						.goods-head {
							.goods-title {
								.goods-name {
									font-weight: 700;
									color: $color-base-090;
									@include font-body(medium);
								}
							}
	
							.keyword-area {
								display: flex;
								flex-wrap: wrap;
								margin: 0.5rem 0;
								.keyword {
									margin: 0.125rem 0.125rem 0 0;
								}
							}
						}

						.data-display {
							.data-item {
								+.data-item {
									box-shadow: 0 -0.0625rem 0 0 $color-base-020;
								}
	
								a.data-area {
									&::after {
										content: '';
										display: block;
										margin-left: 0.5rem;
										width: 1rem;
										height: 1rem;
										background-repeat: no-repeat;
										background-size: 100% auto;
										background-position: center center;
										background-image: svgIcon(chevron-right, $color-primary-070);
									}
								}
								.data-area {
									display: flex;
									justify-content: space-between;
									align-items: center;
									padding: 0.875rem 0;
									text-decoration: none;
	
									.data-head {
										.data-name {
											color: $color-primary-080;
											@include font-body(small);
											font-family: 'Lato', sans-serif;
										}
	
										.mark {
											margin-left: 0.5rem;
										}
									}
	
									.data-body {
										flex-grow: 1;
										text-align: right;
	
										.goods-price {
											display: inline-block;
											
											.price-sale {
												color: $color-base-090;
												@include font-body(small);
												font-family: 'Lato', sans-serif;
												
												.unit {
													// @include font-body(x-small);
												}
			
												.calc {
													@include font-body(x-small);
													color: $color-base-060;
												}
											}
										}
									}
								}
							}
						}
	
						.more-data {
							box-shadow: 0 -0.0625rem 0 0 $color-base-020;
							text-align: center;
							.btn {
								width: 100%;
								height: 2.5rem;
							}
						}
					}
				}

				&:not(.active) {
					.data-display {	
						.data-item {
							&:nth-child(n + 4) {
								display: none;
							}		
						}
					}
				}

				&.active {
					.more-data {
						// display: none;
						.btn.symbol-rtl-chevron-under {
							&::after {
								transform: rotate(180deg);
							}
						}
					}
				}
			}
		}
	}
}

.goods-display.style-d.type-a {
	.goods-list {
		margin: 0;
		padding: 0;

		.goods-item {
			list-style: none;
			position: relative;
			padding: 2rem 0;

			+.goods-item {
				box-shadow: inset 0 0.0625rem 0 0 $color-base-030;

				&::before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: calc(50% - 50vw);
					width: 100vw;
					height: 0.0625rem;
					background-color: $color-base-030;
				}
			}

			.goods-area {
				.goods-figure {
					display: flex;
					justify-content: center;
					position: relative;
					margin: 0 auto;
					width: 9.75rem;
					height: 9.75rem;

					.goods-thumbnail {
						display: block;
						width: 9.75rem;
						height: 9.75rem;
						box-shadow: 0 1rem 1.5rem -1rem rgba(0,0,0,0.25);
						.image {
							display: block;
							border-radius: 0.25rem;
						}
					}

					.goods-thumbnail-magnify {
						display: block;
						overflow: hidden;
						position: absolute;
						bottom: 1rem;
						right: calc(-4.25rem / 2);
						width: 4.25rem;
						height: 4.25rem;
						border: 0.125rem solid #fff;
						border-radius: 50%;
						box-sizing: border-box;

						img {
							display: block;
							width: 100%;
							height: 100%;
						}
					}
				}

				.goods-inform {
					display: flex;
					flex-direction: column;
					align-items: stretch;
					margin-top: 2rem;

					.goods-head {
						margin-bottom: 0.5rem;

						.goods-title {
							a.goods-name {
								text-decoration: none;
							}
							.goods-name {
								font-weight: 700;
								@include font-head(small);
								color: $color-base-090;
							}
						}
					}

					.goods-data {
						margin-top: 0.5rem;

						.posts-use {
							display: flex;
							@include font-body(small);

							.head {
								margin-right: 0.5rem;
								font-weight: 700;
								color: $color-base-090;
								white-space: nowrap;
							}
							.body {
								color: $color-base-070;
							}
						}
					}

					.goods-body {
						.para {
							margin: 0;
							@include font-body(small);
							color: $color-base-070;
						}
					}

					.button-area {
						margin-top: 1rem;

						.btn.symbol-rtl-chevron-under.active::after {
							transform: rotate(180deg);
						}
					}
				}
			}

			.goods-side {
				margin-top: 1rem;
				padding-top: 1rem;
				box-shadow: inset 0 0.0625rem 0 0 $color-base-030;	
			}

			&:not(.active) {
				.goods-side {
					display: none;
				}
			}
		}
	}
}

