	}
}

.goods-display.style-f{
	position: relative;

	&.type-a {
		.goods-list {
			padding: 0;
			margin: 0;

			.goods-item {
				box-shadow: 0 2px 4px 0 rgba(0,0,0,0.08);

				+.goods-item {
					margin-top: 1rem;
				}

				.goods-area {
					.goods-figure {
						overflow: hidden;
						border-top-right-radius: 0.25rem;
						border-top-left-radius: 0.25rem;
					}

					.goods-inform {
						padding: 1rem 1rem 1.5rem;
						@include goods-inform;
					}
				}
			}
		}

		&.large {
			.goods-list {
				.goods-item {
					.goods-area {
						.goods-inform {
							position: relative;
							display: flex;

							.goods-head {
								order: 1;
								margin-bottom: 0.25rem;
								
								.goods-title {
									.goods-name {
										display: flex;
										@include font-body(small);
										color: $color-base-090;
										font-weight: 700;
									}
								}
							}

							.goods-body {
								order: 2;

								.goods-summary {
									a {
										@include font-body(small);
										color: $color-base-070;
										@include ellipsis(3);
									}

									a:not(:hover) {
										text-decoration: none;
									}
								}

								.feature-list {
									display: flex;
									flex-wrap: wrap;
									align-items: center;
									position: absolute;
									right: 1rem;
									bottom: 1.625rem;
									
									.feature-item {
										font-weight: 700;
										color: $color-base-070;
										@include font-body(xx-small);

										& + .feature-item {
											margin-left: 0.5rem;
										}
							
										&.lowest-price,
										&.coupon,
										&.free-shipping {
											color: $color-accent-070;
										}
							
										&.cumulative-sales {
											font-weight: 400;
											color: $color-base-060;
											justify-self: end;
										}
									}
								}
							}

							.goods-foot {
								order: 3;
								margin-top: 0.75rem;

								.goods-price {
									align-items: center;
									.price-sale {
										.value {
											@include font-body(small);
											font-weight: 400;
										}

										.unit {
											@include font-body(x-small);
										}
									}

									.price-discount {
										margin-left: 0.25rem;

										.value {
											@include font-body(x-small);
										}

										.unit {
											@include font-body(x-small);
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

.goods-display.style-g{
	position: relative;

	&.type-a {
		.goods-list {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin: 0;
			padding: 0;
			list-style: none;
			overflow: hidden;

			.goods-item {
				position:relative;
				overflow: hidden;
				z-index: 1;
				
				&:nth-child(1) {
					width: 66.25%;
					padding-top: 66.25%;
					border-radius: 0.25rem 0 0 0.25rem;
				}

				&:nth-child(2) {
					width: 33.125%;
					padding-top: 32.5%;
					border-radius: 0 0.25rem 0 0;
				}

				&:nth-child(3) {
					position: absolute;
					right: 0;
					bottom: 0;
					width: 33.125%;
					padding-top: 32.5%;
					border-radius: 0 0 0.25rem 0;
				}

				.goods-area {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					z-index: 1;

					.goods-figure {
						.goods-thumbnail {
							display: block;

							.image {
								display: block;
							}
						}
					}
				}
			}
		}

		.more-data {
			position: absolute;
			right: 0;
			bottom: 0;
			width: 33.125%;
			padding-top: 32.5%;

			.btn {
				position: absolute;
				top: 0;
				right:0;
				bottom: 0;
				left: 0;

				display: flex;
				justify-content: center;
				align-items: center;
				font-weight: 700;
				@include font-head(medium);
				color: #fff;
				text-decoration: none;
				border-radius: 0 0 0.25rem 0;
				background-color: rgba(0,0,0,0.4);
				z-index: 2;
			}
		}
	}
}

