// 상점 목록
.shop-list {
	.shop-item {
		+.shop-item {
			margin-top: 1rem;
		}

		.shop-head {
			display: flex;
			align-items: center;
			height: 3rem;
			box-shadow: inset 0 -0.0625rem 0 $color-base-090;

			.shop-title {
				display: inline-flex;
				align-items: center;
				
				.shop-name {
					@include font-body(small);
					font-weight: 700;
					color: $color-base-090;
				}

				.shop-data {
					margin-left: 0.25rem;
					@include font-body(small);
					color: $color-base-050;
				}
			}

			.form {
				margin-right: 0.25rem;
			}
		}
		.shop-body {
			>*{
				margin: 1rem 0;
			}

			.add-more {
				display: flex;
				justify-content: flex-end;
				margin-top: 1rem;
			}

			.total-amount {
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 0.75rem 1rem;
				border-radius: 0.25rem;
				background-color: $color-base-010;
		
				.head {
					display: flex;
					align-items: center;
					flex-wrap: wrap;
		
					.tem-name {
						display: block;
						margin-right: 0.25rem;
						@include font-body(small);
						color: $color-base-090;
					}
		
					.delivery-fee {
						display: block;
						@include font-body(x-small);
						color: $color-base-060;
					}
				}
		
				.body {
					.price-data {
						color: $color-base-090;
		
						.value {
							font-weight: 700;
							@include font-body(medium);
						}
		
						.unit {
							font-weight: 700;
							@include font-body(small);
						}
					}
				}
			}
		}
	}
}

.orders-data.style-a {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 1rem 0;

	.data-list {
		display: flex;
		justify-content: center;

		.data-item {
			display: block;

			.data-area {
				display: flex;
				@include font-body(small);

				.data-head {
					margin-right: 0.25rem;;
					color: $color-base-060;
				}

				.data-body {
					font-weight: 700;
					color: $color-base-090;
				}
			}

			.section-name {

			}
		}	
	}
	.button-area {
		margin-top: 0.5rem;
	}
}

.orders-data.style-b {
	position: relative;
	padding: 1rem 0;

	&::before {
		content: '';
		display: block;
		position: absolute;
		z-index: -1;
		top: 0;
		bottom: 0;
		left: calc(50% - 50vw);
		width: 100vw;
		background-color: $color-primary-090;
	}
	
	.data-list {
		display: flex;
		justify-content: space-between;
		align-items: center;

		.data-item {
			display: block;

			.data-area {
				display: flex;
				align-items: center;
				@include font-body(x-small);
				color: #fff;

				.data-head {
					display: block;
					margin-right: 0.25rem;;
				}
				.data-body {
					display: block;
				}
			}

			&.date {
				.data-head {
					@include hidden;
				}
				.data-body {
					font-weight: 700;
				}
			}

			&.deadline {
				.data-body {
					display: flex;
					align-items: cneter;

					.d-day {
						position: relative;
						margin-left: 0.5rem;
						padding-left: 0.5rem;

						&::before {
							content: '';
							display: block;
							position: absolute;
							top: calc(50% - 0.5em);
							left: 0;
							width: 0.0625rem;
							height: 1em;
							background-color: rgba(255,255,255,0.25);
						}
					}
				}
			}
		}

		a.data-area {
			text-decoration: none;
			&::after {
				content: '';
				display: block;
				width: 1rem;
				min-width: 1rem;
				height: 1rem;
				margin-left: 0.25rem;
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 1rem 1rem;
				background-image: svgIcon(chevron-right, #fff);
			}
		}
	}
	.button-area {
		margin-top: 0.5rem;
	}
}

.order-list {
	.order-item {
		+.order-item {
			position: relative;
			border-top: 0.5rem solid #f4f4f4;

			&::before {
				content: '';
				display: block;
				position: absolute;
				left: calc(50% - 50vw);
				top: -0.5rem;
				width: 100vw;
				height: 0.5rem;
				background-color: #f4f4f4;
			}
		}
	}
	.goods-display {
		margin: 1rem 0;
	}

	+.button-area {
		position: relative;
		padding: 1.5rem 0 2rem 0;
		background-color: $color-base-020;

		&::before {
			content: '';
			display: block;
			position: absolute;
			z-index: -1;
			top: 0;
			bottom: 0;
			left: calc(50% - 50vw);
			width: 100vw;
			background-color: inherit;
		}
	}
}